<h2 id="申请使用插件" tabindex="-1"><a class="header-anchor" href="#申请使用插件" aria-hidden="true">#</a> 申请使用插件</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$pluginAppId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'xxxxxxxxx'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin</span><span class="token operator">-></span><span class="token function">apply</span><span class="token punctuation">(</span><span class="token variable">$pluginAppId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="删除已添加的插件" tabindex="-1"><a class="header-anchor" href="#删除已添加的插件" aria-hidden="true">#</a> 删除已添加的插件</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$pluginAppId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'xxxxxxxxx'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin</span><span class="token operator">-></span><span class="token function">unbind</span><span class="token punctuation">(</span><span class="token variable">$pluginAppId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="查询已添加的插件" tabindex="-1"><a class="header-anchor" href="#查询已添加的插件" aria-hidden="true">#</a> 查询已添加的插件</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="获取当前所有插件使用方" tabindex="-1"><a class="header-anchor" href="#获取当前所有插件使用方" aria-hidden="true">#</a> 获取当前所有插件使用方</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$page</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$size</span> <span class="token operator">=</span> <span class="token number">10</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin_dev</span><span class="token operator">-></span><span class="token function">getUsers</span><span class="token punctuation">(</span><span class="token variable">$page</span><span class="token punctuation">,</span> <span class="token variable">$size</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="同意插件使用申请" tabindex="-1"><a class="header-anchor" href="#同意插件使用申请" aria-hidden="true">#</a> 同意插件使用申请</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$appId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'wxxxxxxxxxxxxxx'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin_dev</span><span class="token operator">-></span><span class="token function">agree</span><span class="token punctuation">(</span><span class="token variable">$appId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="拒绝插件使用申请" tabindex="-1"><a class="header-anchor" href="#拒绝插件使用申请" aria-hidden="true">#</a> 拒绝插件使用申请</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin_dev</span><span class="token operator">-></span><span class="token function">refuse</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'拒绝理由'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="删除已拒绝的申请者" tabindex="-1"><a class="header-anchor" href="#删除已拒绝的申请者" aria-hidden="true">#</a> 删除已拒绝的申请者</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">plugin_dev</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>